.page {
  font-family: ' Manrope ', sans-serif;
  color: $main-text-color;
  font-size: 16px;
  line-height: 140%;
  max-width: 100%;
  scroll-behavior: smooth;

  &__body {
    margin: 0;
    min-width: 320px;
  }
  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transition: all 0.3s;
    opacity: 0;
    transform: translateX(-100%);
    pointer-events: none;

    &:target {
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
    }
  }
}

@media (orientation: landscape) {
  .page:has(.page__menu:target) {
    overflow: hidden;
  }

  .page__menu {
    max-height: 100vh;
    overflow-y: auto;
  }
}
